.navbar{
    padding: 1% 2%;
    display: flex;
    flex-direction: row;
justify-content: space-between;
position: sticky;
border-bottom: 2px solid white;

}

.left_nav{
    flex:1;

    display: flex;
align-items: center;
    text-align: center;
}
.left_nav>img{
    width: 29%;
    margin-right: 7%;
}

.right_nav{
    flex: 1;
    text-align: center;
    align-items: center;
}
.right_nav>ul{display: flex;
    
    text-align: center;
justify-content: space-between;}

.right_nav>ul>li{
    align-self: center;
list-style-type:none;
font-weight: 500;
margin-top: 3%;
font-size: 130%;
}